<template>
  <div class="container">
    <div class="form">
      <query-form :budget="budget" ref="queryForm">
        <el-form-item label="是否最新" prop="isUpToDate">
          <el-select v-model="budget.isUpToDate">
            <el-option value="1" label="是"></el-option>
            <el-option value="0" label="否"></el-option>
          </el-select>
        </el-form-item>
        <template slot="last">
          <el-form-item label="申报状态">
            <el-checkbox-group v-model="budget.status">
              <el-checkbox label="1" name="type">未申报</el-checkbox>
              <el-checkbox label="2" name="type">待审批</el-checkbox>
              <el-checkbox label="3" name="type">已审批</el-checkbox>
              <el-checkbox label="4" name="type">审批驳回</el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </template>
      </query-form>
    </div>
    <div class="option">
      <el-button type="primary" size="small" plain @click="resetForm('queryForm')">重置</el-button>
      <el-button type="primary" size="small" plain @click="query">查询</el-button>
    </div>
    <div class="data">
      <div class="table">
        <base-table
          :columns="columns"
          :data="currentData"
          rowKey="version"
          :pagination="true"
          :total="data.length"
          @detail="detail"
          @dataChange="dataChange">
        </base-table>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
const QueryForm = () => import('../QueryForm')
const BaseTable = () => import('@/components/BaseTable')

export default {
  name: 'BudgetList',
  components: {QueryForm, BaseTable},
  data () {
    return {
      currentPage: 1,
      pageSize: 5,
      budget: {
        formulateInstitution: '',
        type: '',
        channel: '',
        timeSlot: '',
        version: '',
        isUpToDate: '',
        status: [],
        bore: '',
        productType: ''
      },
      columns: [
        {prop: 'subInstitution', label: '机构', width: 120},
        {prop: 'type', label: '预算类型', width: 100},
        {prop: 'channel', label: '预算渠道', width: 100},
        {prop: 'timeSlot', label: '预算时间段', width: 100},
        {prop: 'bore', label: '预算口径', width: 100},
        {prop: 'productType', label: '业务分类', width: 100},
        {prop: 'version', label: '版本', width: 120},
        {prop: 'status', label: '状态', width: 100},
        {prop: 'splitStatus', label: '拆分状态', width: 100},
        {prop: 'approvaler', label: '下步审批人', width: 100},
        {
          prop: 'operation',
          label: '操作',
          width: 100,
          operation: true,
          operations: [{text: '详情', event: 'detail'}]}
      ],
      data: [
        {
          subInstitution: '北京分公司',
          type: '年度预算',
          channel: '传统渠道',
          timeSlot: '2019',
          bore: '保单年口径',
          productType: '全部',
          version: '1',
          status: '未申报',
          splitStatus: '已拆分',
          approvaler: '张三'
        },
        {
          subInstitution: '上海分公司',
          type: '年度预算',
          channel: '传统渠道',
          timeSlot: '2019',
          bore: '保单年口径',
          productType: '全部',
          version: '2',
          status: '未申报',
          splitStatus: '已拆分',
          approvaler: '张三'
        },
        {
          subInstitution: '江苏省分公司',
          type: '年度预算',
          channel: '传统渠道',
          timeSlot: '2019',
          bore: '保单年口径',
          productType: '全部',
          version: '3',
          status: '未申报',
          splitStatus: '已拆分',
          approvaler: '张三'
        },
        {
          subInstitution: '山东省分公司',
          type: '年度预算',
          channel: '传统渠道',
          timeSlot: '2019',
          bore: '保单年口径',
          productType: '全部',
          version: '4',
          status: '未申报',
          splitStatus: '已拆分',
          approvaler: '张三'
        },
        {
          subInstitution: '山西省分公司',
          type: '年度预算',
          channel: '传统渠道',
          timeSlot: '2019',
          bore: '保单年口径',
          productType: '全部',
          version: '5',
          status: '未申报',
          splitStatus: '已拆分',
          approvaler: '张三'
        }
      ]
    }
  },
  computed: {
    ...mapState({
      tableHeader: state => state.app.tableHeader
    }),
    currentData () {
      const pageData = []
      const start = (this.currentPage - 1) * this.pageSize
      let end = this.currentPage * this.pageSize
      end = end <= this.data.length ? this.currentPage * this.pageSize : this.data.length
      for (let i = start; i < end; ++i) {
        pageData.push(this.data[i])
      }
      return pageData
    },
    timeSlots () {
      const result = []
      const type = this.budget.type + ''
      if (type === 'month') {
        result.push({value: '201801', label: '2018第一季度'})
        result.push({value: '201802', label: '2018第二季度'})
        result.push({value: '201803', label: '2018第三季度'})
        result.push({value: '201804', label: '2018第四季度'})
        this.resetTime('201801')
      } else if (type === 'year') {
        result.push({value: '2018', label: '2018'})
        this.resetTime('2018')
      }
      return result
    }
  },
  methods: {
    query () {
      this.currentPage = 1
    },
    detail (version) {
      this.$router.push({path: `/admin/budget/detail/${version}`})
    },
    resetForm (formName) {
      this.$refs[formName].reset()
      this.budget.status = []
    },
    resetTime (val) {
      this.budget.timeSlot = val
    },
    dataChange (size, page) {
      this.pageSize = size
      this.currentPage = page
    }
  }
}
</script>



